import { Component, OnInit } from '@angular/core';

import { ProfessorService } from '../../angular-services/professor.service';
import { Professor } from '../../classes/professor';

@Component({
  selector: 'app-list-professors-admin',
  templateUrl: './list-professors.component.html',
  styleUrls: ['./list-professors.component.css']
})
export class ListProfessorsComponent implements OnInit {

  professors: Professor[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private professorService: ProfessorService) { }

  ngOnInit() {
    this.getProfessors();
  }

  getProfessors() {
    this.professorService.getProfessors().subscribe(
      response => {
        if (response !== undefined) {
          this.professors = response;
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao carregar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  removeProfessor(professor) {
    let confirm = prompt('Para confirmar esta operação digite SIM: \n\n ');
    if (confirm === 'SIM') {
      this.professors = this.professors.filter(t => t !== professor);
      this.professorService.removeProfessor(professor._key).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Professor excluído com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao carregar os dados!';
            this.alert.show = true;
          }
        }
      )
    } else {
      this.alert.type = 'danger';
      this.alert.message = 'O captcha inválido!';
      this.alert.show = true;
    }
  }

}
